import React, { useEffect } from 'react';
import { Link } from 'reaouter';
import { Navibar } from 'Templates';
import { isColorDark, colorShade } from 'Common/Utils';
import { useLocal } from 'Source/App/Storage/Local';
import { localkey } from 'Source/App/Main/local-config';
import N from 'Source/App/Main/Pages/Neumorphism/scripts';

const Header = () => {
	const { setLocal, getLocal } = useLocal(localkey.general)
	const { color } = getLocal()
	const isDark = isColorDark(color)
	const handleNightMode = (e) => {
		const isChecked = e.target.checked  
		setLocal(prev => ({
			...prev, 
			color: isChecked ? '#5f5f5f' : '#f2f2f2',
			textColor: isChecked ? colorShade( '#fff', -10): '#496683',
		}))
	}
	useEffect(() => {
		const dropdowns = Array.from(document.querySelectorAll('#navigation-bar .n-dropdown'))
		const instances = dropdowns.map(dropdown => N.dropdown.getInstance(dropdown))
		return () => instances.forEach(instance => instance.destroy());
	}, [])
	return (
		<header id="navigation-bar" className="header n-flat-low n-bordered fixed-top">
			<div className="sub-menu n-text-color">
				<div className="sub-nav mr-auto">
					<Link className="sub-link" to='/' label="creator's profile" />
				</div>
				<div className="sub-nav">
					<div className="n-dropdown" data-interactive="true">
						<a className="n-dropdown-toggle sub-link" role="button" aria-haspopup="true" aria-hidden="true">
							<i className="mytabworks icon-cog-alt"/>
						</a>
						<div className="n-dropdown-menu" style={{minWidth: '200px'}}>
							<div className="n-dropdown-content">
								<div className="n-dropdown" data-interactive="true"> 
									<a 
										className="n-dropdown-item n-dropdown-toggle" 
										href="#">
										<i className="mytabworks icon-moon"/>
										Dark theme: {isDark ? 'On' : 'Off'}
									</a>
									<div className="n-dropdown-menu">
										<div className="n-dropdown-content py-2 px-3" style={{minWidth: '300px'}}>
											<p>Dark theme turns the light surfaces of the page dark, creating an experience ideal for night. Try it out!</p> 
											<p>Your Dark theme setting will apply to this browser only.</p>
											<div className="d-flex flex-center-y">
												<h5>DARK THEME</h5> 
												<div className="n-switch ml-auto">
													<input id="n-mode" type="checkbox" checked={isDark} onChange={handleNightMode} />
													<label className="n-switch-label" htmlFor="n-mode"></label>
												</div>
											</div> 
										</div>
									</div>
								</div>  
								<a 
									className="n-dropdown-toggle n-dropdown-item" 
									href="#">
									<i className="mytabworks icon-language"/>
									<del>Language: English</del>
								</a>
								<div className="n-dropdown" data-interactive="true"> 
									<a 
										className="n-dropdown-toggle n-dropdown-item" 
										href="#">
										<i className="mytabworks icon-question-mark"/>
										About this site
									</a>
									<div className="n-dropdown-menu">
										<div className="n-dropdown-content py-2 px-3" style={{minWidth: '300px'}}>
											<p>
												Mytabworks's projects, libraries, and all it's content was occasionally made during the pandemic lockdown which is started in mid march of 2020 and deploy in august 2020.
											</p> 
											<p>
												Almost 90.99% of the app was handmadely coded from scratch by the creator itself. the only components used in this site which is not done by the creator was the react-live, jszip, icons and tools images.
											</p>
										</div>
									</div>
								</div> 
							</div>
						</div>
					</div>
				</div>
			</div>
			<Navibar as="div" className="main-menu">
				<Link
					className="navibar-brand n-border-color"
					to='/' 
					label={
						<>
							<img className="brand-logo" src='/images/icons/icon-72x72.png' alt='mytabworks' />{' '}
							<span className="brand-name" data-color-changable data-phone-show>Mytabworks</span>
						</>
					}
				/>
				<button className="navibar-toggle n-btn" data-collapse="#navibar-collapse" aria-expanded="false">
					<i className="mytabworks icon-burger"/>
				</button>
				<div className="navibar-collapse" id="navibar-collapse"> 
					<div className={`n-nav n-no-hover mr-auto nav-align`}> 
						<Link className="n-nav-link" nested={true} to='/react' label='React Libraries' />
						<Link className="n-nav-link" nested={true} to='/javascript' label='Javascript Libraries' />
						<Link className="n-nav-link" nested={true} to='/neumorphism' label='Neumorphism Project' />
					</div>
				</div>
			</Navibar>
		</header>
	);
}

export default Header